.phoneCode-outer {
    display: block;
    width: calc(100% - 30px);
    height: 48px;
    margin: 15px 15px;
    /* border: 2px solid rgb(67, 67, 67) */
}

.phoneCode-text {
    float: left;
    width: 23%;
    height: 100%;
    font-size: 3vw;
    text-align: center;
    line-height: 45px;
}

.phoneCode-select {
    width: 77%;
    height: 100%;
}
.ant-select-show-search.ant-select:not(.ant-select-customize-input) {
    /* height: 44px !important; */
    /* color: red !important; */
}
.phoneCode-select>div.ant-select-selector{
    font-size: 18px !important;
    height: 100% !important;
    border: 2px solid rgb(67, 67, 67) !important;
    padding-top: 7px !important;
}
.phoneCode-select>div.ant-select-selector>span.ant-select-selection-search>input{
    height: 100% !important;
}
